<template>
  <div class="list">
      <button @click="prev"><</button>
      <film-preview v-for="(film,index) in films.data" :key="index" :film="film">
      </film-preview>
      <button @click="next">></button>
  </div>
</template>

<script>
import FilmPreview from '../components/FilmPreview'
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['films'])
  },
  methods: {
    ...mapActions(['getFilms', 'getNext']),
    prev() {
        if(this.films.prev_page_url) {
            this.router.push({ name: "List", params:{page:this.$route.params.page-1}})
        }
    },
    next() {
        if(this.films.next_page_url) {
            this.router.push({ name: "List", params:{page:this.$route.params.page+1}})
        }
    }
  },
  mounted() {
        this.getNext(this.$route.params.page)
  },
  watch: {
    $route(to, from) {
        this.getNext(this.$route.params.page)
    }
  },
  components: {
    FilmPreview
  }
}
</script>
<style>
  .home {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>

